.btn-outline-secondary:active:focus,
.btn-outline-secondary:focus {
  box-shadow: none;
}

.btn-outline-secondary,
.btn-outline-secondary:active,
.btn-outline-secondary:hover {
  background-color: unset;
  color: #111;
  border-color: #111;
}
.btn-danger:hover,
.btn-danger:active,
.btn-danger:focus,
.btn-danger:active:focus {
  border-color: unset;
  background-color: #dc3545;
  box-shadow: none;
}

.btn-search-section {
  display: flex;
  justify-content: space-between;
}

.btn-search-section .btn {
  height: 38px;
}

.has-search {
  width: 100%;
  max-width: 500px;
  align-self: center;
}
.has-search .form-control {
  padding-left: 2.375rem;
  background: rgb(248, 246, 246);
  padding-top: 8px;
  padding-bottom: 8px;
  border: none;
}
.form-control:focus {
  box-shadow: none;
}
.has-search .form-control-feedback {
  position: absolute;
  z-index: 2;
  display: block;
  width: 2.375rem;
  height: 2.375rem;
  line-height: 2.375rem;
  text-align: center;
  pointer-events: none;
  color: #aaa;
}

.filter .btn-outline-secondary {
  color: #dc3545;
}

.add-edit-modal input::-webkit-outer-spin-button,
.add-edit-modal input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
.add-edit-modal input[type='number'] {
  -moz-appearance: textfield;
}

label {
  opacity: 0.8;
}

.filter_btn span {
  margin-right: 15px;
}
.filter_btn {
  margin: 0px 8px;
  font-size: 14px;
}
.filter_btn.btn-secondary:focus,
.filter_btn.btn-secondary:hover {
  box-shadow: none;
  background-color: #6c757d;
  border-color: #6c757d;
}

.sub-header-component {
  background-color: #12284c;
  color: #ffffff;
  font-size: 14px;
}

.vertical-align-text-bottom {
  vertical-align: text-bottom;
}

.btn-search-section .add-btn {
  min-width: 165px;
  margin-top: 1px;
}

.filter-btn {
  height: 38px;
}

@media screen and (max-width: 1024px) {
  .has-search {
    max-width: 300px;
  }
}

@media screen and (min-width: 1200px) and (max-width: 1399px) {
  .has-search {
    max-width: 400px;
  }
}

